.NavParent{
    width: 100%;
    top:0;
    left:0;
    right:0;   
}

.NavChild{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding:0.3cm 5vw;
    /* border-bottom: thin solid rgb(53, 53, 53); */
}

.Menu{
    display: flex;
    flex-direction: row;
    column-gap: 0.4cm;
    align-items: center;
}

.LogoImg{
    width:6cm;
    height:auto;
}

.itemLight{
    font-family: nunito sans;
    color:rgb(28, 28, 28);
    font-size: 16px;
    transition: .3s all ease;
    border-bottom: 2px solid transparent;
    text-decoration: none;
    
}

.itemLight:hover{
    /* color:rgb(180, 112, 253); */
    border-bottom: 2px solid rgb(171, 93, 254);
}
.Dark{
    color:rgb(238, 238, 238);
    font-size: 16px;
}

.signOutBtn{
    width:0.8cm;
    height: auto;
    cursor: pointer;
}

.active{
    /* color:rgb(171, 93, 254); */
    border-bottom: 2px solid rgb(171, 93, 254);
}