* {
  margin: 0;
  padding: 0;
  font-family: Nunito Sans;
}

/* signIn */
.signInParent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: auto;
  height: 100vh;
  position: relative;
  width: 100vw;
}

.signInParent::before{
  content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-image: url("lib.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    background-color: white;
    filter: brightness(25%);
}

input:-webkit-autofill {
  -webkit-text-fill-color: white;
  -webkit-box-shadow: 0 0 0 1000px rgb(39, 39, 39) inset;
  /* This will set the background color to light blue */
}

.signInHeading {
  font-size: 22px;
  font-weight: 600;
  color: #fdffff;
}

