}

.Site::after {
  position: fixed;
  content: "";
  inset: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
 
  background-image: url("lib2.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
